import React from 'react';
import './TopTitleContainer.scss';

import AnimateTextContainer from '../../components/AnimateTextContainer/AnimateTextContainer';

export default class TopTitleContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dimensions: {}
        };
    }
    /* Lifecycle Evenets */
    componentDidMount() {
        this.setState({
            dimensions: {
                height: window.screen.height
            }
        });
    }
    render() {
        const { dimensions } = this.state;
        return (
            <div className="top-title-container" style={{ minHeight: dimensions.height + 'px' }}>
                <div className="animated-heading centered">
                    <AnimateTextContainer></AnimateTextContainer>
                </div>
                <div className="description centered">
                    <span>Having 9 years of software development expertise in various domains like mobile and web development,
                    making effective use of Computer Engineering degree in my profession.
                    Currently working as a Lead Full Stack Developer for Client Baker Hughes.
                </span>
                </div>
            </div>
        );
    }
}
