import React from 'react';
import './SkillsContainer.scss';
import svgFrontend from '../../assets/svg/frontend.svg';
import svgMobile from '../../assets/svg/mobile.svg';
import svgBackend from '../../assets/svg/backend.svg';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default class SkillsContainer extends React.Component {
  constructor(props) {
    super(props);
    AOS.init();
    this.state = {
      dimensions: {}
    };
  }
  /* Lifecycle Evenets */
  componentDidMount() {
    this.setState({
      dimensions: {
        height: window.screen.height
      }
    });
  }
  render() {
    const { dimensions } = this.state;
    return (
      <div className="skills-container" style={{ minHeight: dimensions.height + 'px' }}>
        <div className="common-title-container">
          <span>SKILLS</span>
        </div>
        <div className="skills-image-container">
          <div className="image-container"
            data-aos="fade-left"
            data-aos-offset="100"
            data-aos-delay="400"
            data-aos-duration="1000">
            <img src={svgFrontend} alt="Frontend" />
          </div>
          <div className="image-container">
            <img src={svgBackend} alt="Backend" />
          </div>
          <div className="image-container"
            data-aos="fade-right"
            data-aos-offset="100"
            data-aos-delay="400"
            data-aos-duration="1000">
            <img src={svgMobile} alt="Mobile" />
          </div>
        </div>
      </div>
    );
  }
}
