import React from 'react';
import './AnimateTextContainer.scss';

export default class AnimateTextContainer extends React.Component {
  strText = "FULL STACK ENGINEER";
  textIntervals = [];
  constructor(props) {
    super(props);
  }
  /* Custom Methods */
  loadText() {
    for (let index = 0; index < this.strText.length; index++) {
      const currentChar = this.strText[index];
      const spanElement = document.createElement("span", []);
      spanElement.innerHTML = currentChar;
      const strId = "text" + index + "Id";
      spanElement.setAttribute("id", strId);
      spanElement.style.position = "absolute";
      spanElement.style.width = "25px";
      spanElement.style.transitionDuration = "1s";
      if (index >= 0 && this.strText[index - 1] === ' ') {
        spanElement.style.width = "16px";
      }
      spanElement.style.left = index * 30 + 'px';
      spanElement.style.top = "0px";
      const animatedTextContainer = document.getElementById("animatedTextId");
      animatedTextContainer.appendChild(spanElement);
    }
    this.animate();
  }
  animate() {
    for (let index = 0; index < this.strText.length; index++) {
      const strId = "text" + index + "Id";
      const animatedChar = document.getElementById(strId);
      const interval = setInterval(() => {
        if (animatedChar.style.top === "0px") {
          animatedChar.style.top = '-20px';
        } else {
          animatedChar.style.top = 0;
        }
      }, Math.floor(Math.random() * 900) + 500);
      this.textIntervals.push(interval);
    }
  }
  clearAnimate() {
    for (let index = 0; index < this.textIntervals.length; index++) {
      const interval = this.textIntervals[index];
      clearInterval(interval);
    }
  }
  /* Lifecycle Events */
  componentDidMount() {
    this.loadText();
  }
  render() {
    return (
      <div id="animatedTextId" className="animated-text">
      </div>
    );
  }
}
