import React from 'react';
import './DetailsPopup.scss';

export default class DetailsPopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            project: this.props.data
        }
    }
    loadData() {

    }
    closePopup() {
        this.props.close()
    }
    updateData(data) {
        this.setState({
            project: data
        });
    }
    /*Lifecycle hooks */
    componentDidMount() {
        this.loadData();
    }
    render() {
        const { project } = this.state;
        return (
            <div className="details-popup">
                {(project || {}).title &&
                    <div className="details-popup-container">
                        <div className="title-container">
                            <div className="title">{(project || {}).title}</div>
                            <div className="close-button" onClick={this.closePopup.bind(this)}>
                                <span>x</span>
                            </div>
                        </div>
                        <div className="description"> {(project || {}).summary}</div>
                        <div className="bullet-points">
                            <ul>
                                {project.bullets.map((point, i) => (
                                    <li key={i}>{point}</li>
                                ))
                                }
                            </ul>
                        </div>
                    </div>
                }
            </div>
        )
    }
}