import React from 'react';
import './ProjectsContainer.scss';
import BurstCircle from '../../components/BurstCircle/BurstCircle';
import DetailsPopup from '../../components/popup/DetailsPopup';
import * as ProjectModel from '../../models/project';

export default class ProjectsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dimensions: {},
      popup: {
        isVisible: false
      },
      projects: ProjectModel.getProjects(),
      selectedProject: null,
      burstAnimateOriginsStates: []
    };
    this.popupRef = React.createRef();
    this.burstCircleContainerRef = React.createRef();
    this.burstCircleRefs = [];
    this.burstAnimateOrigins = []
    for (let index = 0; index < this.state.projects.length; index++) {
      this.burstCircleRefs[index] = React.createRef();
      this.burstAnimateOrigins.push({});
    }
  }
  burstCircleClicked(data) {
    this.setState({
      popup: {
        isVisible: true
      },
      selectedProject: data
    });
    if (this.popupRef && this.popupRef.current) {
      this.popupRef.current.updateData(data);
    }
  }
  detailsPopupClose() {
    this.setState(prevState => ({
      popup: {
        isVisible: false
      }
    }));
  }
  animateBurstCircles() {
    const containerRect = this.burstCircleContainerRef.current.getBoundingClientRect();
    const containerOriginY = containerRect.y;
    const containerOriginHeight = containerRect.height;
    for (let index = 0; index < this.burstCircleRefs.length; index++) {
      const ref = this.burstCircleRefs[index];
      const innerRect = ref.current.getBoundingClientRect();
      setTimeout(() => {
        const relativeTop = containerOriginY - innerRect.y;
        this.burstAnimateOrigins[index].top = Object.assign(relativeTop);
        this.burstAnimateOrigins[index].left = index*100;
        this.burstAnimateOrigins[index].isIncrement = true;
        this.burstAnimateInterval = setInterval(() => {
          if (this.burstAnimateOrigins[index].top == relativeTop) {
            this.burstAnimateOrigins[index].top++;
            this.burstAnimateOrigins[index].isIncrement = true;
          } else if (this.burstAnimateOrigins[index].top == (containerOriginHeight - innerRect.height)) {
            this.burstAnimateOrigins[index].top--;
            this.burstAnimateOrigins[index].isIncrement = false;
          } else {
            if (this.burstAnimateOrigins[index].isIncrement) {
              this.burstAnimateOrigins[index].top++;
            } else {
              this.burstAnimateOrigins[index].top--;
            }
            innerRect.y = this.burstAnimateOrigins[index].top;
          }
          this.setState({
            burstAnimateOriginsStates: this.burstAnimateOrigins
          });
        }, 10);
      }, 500);
    }
  }
  animateNow() {
    
  }
  /* Lifecycle Evenets */
  componentDidMount() {
    this.setState({
      dimensions: {
        height: window.screen.height
      }
    });
    this.animateBurstCircles();
  }
  render() {
    const { dimensions, popup, projects, selectedProject, burstAnimateOriginsStates } = this.state;
    const isPopupVisible = popup.isVisible || false;
    return (
      <div className="projects-container" style={{ minHeight: dimensions.height + 'px' }}>
        <div className="common-title-container" style={{ height: 65 + 'px' }}>
          <span>Projects</span>
        </div>
        <div className="projects-content" style={{ minHeight: (dimensions.height - 65) + 'px' }}>
          <div ref={this.burstCircleContainerRef} className="burst-circles">
            <div ref={this.burstCircleRefs[0]}
              style={{ left: ((burstAnimateOriginsStates || [])[0] || {}).left , top: ((burstAnimateOriginsStates || [])[0] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[0]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[1]}
              style={{ left: ((burstAnimateOriginsStates || [])[1] || {}).left , top: ((burstAnimateOriginsStates || [])[1] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[1]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[2]}
              style={{ left: ((burstAnimateOriginsStates || [])[2] || {}).left , top: ((burstAnimateOriginsStates || [])[2] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[2]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[3]}
              style={{ left: ((burstAnimateOriginsStates || [])[3] || {}).left , top: ((burstAnimateOriginsStates || [])[3] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[3]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[4]}
              style={{ left: ((burstAnimateOriginsStates || [])[4] || {}).left , top: ((burstAnimateOriginsStates || [])[4] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[4]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[5]}
              style={{ left: ((burstAnimateOriginsStates || [])[5] || {}).left , top: ((burstAnimateOriginsStates || [])[5] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[5]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
            <div ref={this.burstCircleRefs[6]}
              style={{ left: ((burstAnimateOriginsStates || [])[6] || {}).left , top: ((burstAnimateOriginsStates || [])[6] || {}).top }} className="burst-circle-container">
              <BurstCircle data={(projects || [])[6]} onClick={this.burstCircleClicked.bind(this)}></BurstCircle>
            </div>
          </div>
          <div className="detail-content">
            <DetailsPopup ref={this.popupRef} data={selectedProject} close={this.detailsPopupClose.bind(this)}></DetailsPopup>
          </div>
        </div>
      </div>
    );
  }
}

