import React from 'react';
import './BurstCircle.scss';
import mojs from '@mojs/core';
import * as Helpers from '../../util/helpers';
import * as ProjectModel from '../../models/project';

export default class BurstCircle extends React.Component {
    constructor(props) {
        super(props);
        this.circle = {};
        this.uuid = Helpers.UID();
    }
    loadData() {
        this.project = new ProjectModel.Project(this.props.data);
        this.circle = new mojs.Shape({
            parent: '#bouncyCircleId' + this.uuid,
            shape: 'circle',
            fill: { '#F64040': '#FC46AD' },
            radius: { 50: 70 },
            duration: 500,
            isYoyo: true,
            isShowStart: true,
            repeat: 1,
            onComplete: () => {
                new mojs.Burst({
                    parent: '#bouncyCircleId' + this.uuid,
                    radius: { 0: 100 },
                    count: 5,
                    children: {
                        shape: 'circle',
                        radius: 20,
                        fill: ['deeppink', 'cyan', 'yellow'],
                        strokeWidth: 5,
                        duration: 2000
                    }
                }).replay();
            }
        }).replay();
    }
    clicked() {
        if (this.circle) {
            this.circle.replay();
            this.props.onClick(this.project)
        }
    }
    /*Lifecycle hooks */
    componentDidMount() {
        this.loadData();
    }
    render() {
        return (
            <div id={ "bouncyCircleId" + this.uuid } className="burst-circle" onClick={ this.clicked.bind(this) }>
                <span>{ (this.project || {}).code }</span>
            </div>
        )
    }
}